import {api} from "/ts/util/api";
import opg from '/ts/opg';
import {Languages} from "/ts/util/Languages";
import ViewWishList from "./ViewWishList";
import ViewSchedule from "./ViewSchedule";
import PersonalScore from "./PersonalScore";
import QualifyDate from "./QualifyDate";
import ManualBuyerSchedule from './ManualBuyerSchedule';
import ManualExhibitionSchedule from './ManualExhibitionSchedule';


const lng = Languages.package;

opg('#mainTab').tabView({
    data: [
        {label: lng.tab1, view: '#d1'},
        {label: lng.tab2, view: '#d2'},
        {label: lng.tab3, view: '#d3'},
        {label: lng.tab4, view: '#d4'},
        {label: lng.tab5, view: '#d5'},
        {label: lng.tab6, view: '#d6'}
    ]
});


new ViewWishList(lng);
new ViewSchedule(lng);
new PersonalScore(lng);
new QualifyDate(lng);
new ManualBuyerSchedule(lng);
new ManualExhibitionSchedule(lng);
